<template>
  <ul class="nav nav-mobile-menu">
    <li>
      <a href="#/" class="dropdown-toggle" data-toggle="dropdown">
        <i class="material-icons">home</i>
        <p>Inicio</p></a
      >
    </li>
    <li>
      <a
        class="dropdown-toggle"
        data-toggle="dropdown"
        v-if="autenticado"
        @click="Logout()"
      >
        <i class="material-icons">exit_to_app</i>
        <p>Exit</p></a
      >
    </li>
    <li>
      <a
        href="#/login"
        v-if="!autenticado"
        class="dropdown-toggle"
        data-toggle="dropdown"
      >
        <i class="material-icons">vpn_key</i>
        <p>Login</p></a
      >
    </li>
    <li>
      <a
        href="#/register"
        v-if="!autenticado"
        class="dropdown-toggle"
        data-toggle="dropdown"
      >
        <i class="material-icons">person_add</i>
        <p>Register</p></a
      >
    </li>
  </ul>
</template>
<script>
export default {
  data() {
    return {
      estatus: false,
      autenticado: false,
      search: null,
      selectedEmployee: null,
      employees: [
        "Jim Halpert",
        "Dwight Schrute",
        "Michael Scott",
        "Pam Beesly",
        "Angela Martin",
        "Kelly Kapoor",
        "Ryan Howard",
        "Kevin Malone"
      ]
    };
  },
  created() {
    if (
      !localStorage.getItem("Rol") > 0 &&
      localStorage.getItem("Verificacion")
    ) {
      this.Logout();
    }
    this.autenticado = localStorage.getItem("Verificacion");
  },
  methods: {
    Logout() {
      localStorage.removeItem("Verificacion");
      localStorage.removeItem("Rol");
      localStorage.removeItem("Login");
      localStorage.removeItem("Password");
      window.location.href = "#/inicio";
      this.$router.go(0);
    }
  }
};
</script>
